import { Router } from '@angular/router';
import { ValueAnimator } from './../../shared/value.animator';
import { Component, OnInit } from '@angular/core';
import { SvgManager } from "shared/svg.manager";
import { style } from '@angular/animations';

@Component({
	selector: 'app-splash',
	templateUrl: './splash.component.html',
	styleUrls: ['./splash.component.scss']
})
export class SplashComponent implements OnInit {
	private router:Router;
	private bigImage:any;
	private imagesList:any = [
		'20171028_100740',
		'20171028_100804',
		'20180211_041830',
		'20180216_175600',
		'20180216_182038',
		'20180216_182043',
		'claro_estacao',
		'escuro_estacao',
		'IMG-20180131-WA0023',
		'IMG-20180205-WA0017',
		'IMG-20180205-WA0023',
		'IMG-20180520-WA0034',
		'IMG-20180520-WA0035',
		'IMG-20180520-WA0036',
		'IMG-20180520-WA0037',
		'IMG-20180520-WA0038',
		'IMG-20180520-WA0039',
		'IMG-20180520-WA0040',
		'IMG-20180520-WA0041',
		'IMG-20180520-WA0042',
		'IMG-20180520-WA0043',
		'IMG-20180520-WA0044',
		'IMG-20180520-WA0045',
		'IMG-20180520-WA0046',
		'IMG-20180520-WA0047',
		'IMG-20180520-WA0048',
		'IMG-20180520-WA0049',
		'IMG-20180520-WA0050',
		'IMG-20180520-WA0051',
		'IMG-20180520-WA0052',
		'IMG-20180520-WA0053'
	]

	constructor(private svgManager:SvgManager, router:Router) {
		this.router = router;
	}

	ngOnInit() {
		this.fillBackImages();
		this.loadBigImage('../../assets/images/' + this.imagesList[Math.floor(Math.random() * this.imagesList.length)]+'.jpg');
		this.changeHightlight();
	}

	fillBackImages() {
		let auxImagesList: any = Object.assign([], this.imagesList);
		let images: any = document.querySelectorAll('.mosaic__item');
		for (let i = 0; i < images.length; i++) {
			let index = Math.floor(Math.random() * auxImagesList.length);
			images[i].querySelector('.mosaic__item--container--back').style.backgroundImage = 'url(../../assets/images/' + auxImagesList[index] + '.jpg)';
			auxImagesList.slice(index, 1);
		}
	}

	changeHightlight() {
		let current = document.querySelector('.mosaic__item--hit-area.active');
		if(current) {
			current.classList.remove('active');
		}

		let images: any = document.querySelectorAll('.mosaic__item');
		let index = Math.floor(Math.random() * images.length);
		setTimeout(() => {
			images[index].querySelector('.mosaic__item--hit-area').classList.add('active');
		}, 500);

		setTimeout(() => {
			this.changeHightlight();
		}, 2000);
	}

	loadBigImage(path) {
		this.bigImage = new Image();
		this.bigImage.onload = () => {
			let mosaic = document.querySelector('.mosaic');
			let imW = this.bigImage.width;
			let imH = this.bigImage.height;
			let mosaicW = mosaic.getBoundingClientRect().width;
			let mosaicH = mosaic.getBoundingClientRect().height;
			let prop = mosaicW / imW;
			let propInv = imW / mosaicW;
			let transX = 0, transY = 0;
			this.bigImage.width = mosaicW;
			this.bigImage.height = this.bigImage.height * prop;

			if (this.bigImage.height < mosaicH) {
				prop = mosaicH / imH;
				propInv = imH / mosaicH;
				this.bigImage.height = mosaicH;
				this.bigImage.width = this.bigImage.width * prop;
			}

			transX = (this.bigImage.width - mosaicW) * .5;
			transY = (this.bigImage.height - mosaicH) * .5;

			this.drawImages(transX, transY, propInv);
		}

		this.bigImage.src = path;
	}

	drawImages(transX:number, transY:number, prop) {
		let images: any = document.querySelectorAll('.mosaic__item');
		for(let i = 0; i < images.length; i++) {
			var c: any = images[i].querySelector(".mosaic__item--container--front");
			var ctx = c.getContext("2d");
			var l = parseInt(images[i].getBoundingClientRect().left)-10 + transX;
			var t = parseInt(images[i].getBoundingClientRect().top)-10 + transY;
			var w = parseInt(images[i].getBoundingClientRect().width);
			var h = parseInt(images[i].getBoundingClientRect().height);
			c.setAttribute('width', w);
			c.setAttribute('height', h);
			ctx.drawImage(this.bigImage, l * prop, t * prop, w * prop, h * prop, 0, 0, w, h);
		}
	}
}


